html,
body,
#root {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    background-color:grey;
}


.root-container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top:100px;
}

.box-container {
    display: flex;
    /* flex-direction: column; */
    max-width: 35em;
    min-width: 4em;
    /* min-height: 30em; */
    box-shadow: 0px 0px 20px 0px rgba(15, 15, 15, 0.2);
    border-radius: 6px;
    border: 2px solid green;
    padding: 25px;
}
/* $blue_theme: #70a1ff; */
.login-input {
    height: 2.1em;
    border-radius: 3px;
    border: 1px solid #70a1ff;
    transition: border 0.4s ease-out;
    padding: 12px;
    font-family: Oxygen, sans-serif;
    font-size: 19px;
    color: Blue;
    background: rgba(15, 15, 15, 0.01);
}

.center{
    text-align: center;
    align-items: center;
    padding:5px;
}
span.heading{
    font-size: 30px;
    margin: auto;
    text-align: left;
    
    /* margin-left: 40%;
    margin-right: 40%; */
    font-weight: bold;
    color:mediumvioletred
}
.login-input:hover {
    border: 2px solid #70a1ff;
}

.login-input:focus {
    border: 2px solid #6565e7;
    box-shadow: 0px 0px 20px rgba(15, 15, 15, 0.2);
}

label{
    font-weight: bolder;
    color: black;
}

img{
    border:green;
    display: block;
    margin-right: 40%;
    margin-left: 40%;
    justify-items: center;
    padding-top:25px; 
    padding-bottom: 25px;
    align-items: center;
    align-content: center;
    /* height:60%; */
    width: 20%;
}
.profile{
    display:inline
}